import React, { useEffect, useState } from "react";
import { supabase } from "../lib/supabaseClient";
import "./NotificationDropdown.css"; // Add styles for the dropdown

interface Notification {
  id: string;
  user_id: string;
  user_name: string;
  user_profile: string;
}

export default function NotificationDropdown() {
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    // Fetch initial notifications
    const fetchNotifications = async () => {
      const { data, error } = await supabase
        .from("party_requests")
        .select("id, user_id, user_name, user_profile")
        .eq("status", "Pending");

      if (error) {
        console.error("Error fetching notifications:", error);
      } else {
        setNotifications(data || []);
      }
    };

    fetchNotifications();

    // Subscribe to real-time updates
    const subscription = supabase
      .channel('party_requests')
      .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'party_requests' }, (payload: { new: Notification }) => {
        setNotifications((prev) => [payload.new, ...prev]);
      })
      .subscribe();

    return () => {
      supabase.removeChannel(subscription);
    };
  }, []);

  return (
    <div className="notification-dropdown">
      <div
        className="notification-icon"
        onClick={() => setIsOpen((prev) => !prev)}
      >
        <span className="bell-icon">ðŸ””</span>
        {notifications.length > 0 && (
          <span className="notification-badge">{notifications.length}</span>
        )}
      </div>

      {isOpen && (
        <div className="dropdown-menu">
          {notifications.length === 0 ? (
            <p>No new requests</p>
          ) : (
            notifications.map((notification) => (
              <div key={notification.id} className="dropdown-item">
                <img
                  src={notification.user_profile}
                  alt={notification.user_name}
                  className="profile-pic"
                />
                <span className="user-name" title={notification.user_name}>
                  {notification.user_name}
                </span>
              </div>
            ))
          )}
        </div>
      )}
    </div>
  );
}